import { Pie } from 'vue-chartjs';
import * as echarts from "echarts";
import { ref, reactive, toRefs, watch, onMounted } from "vue";
import { ElBreadcrumb, ElBreadcrumbItem } from 'element-plus';
import { useRoute } from 'vue-router';
export default {
  components: {},
  setup() {
    const route = useRoute();
    const pathList = ref();
    const getCurrentPath = () => {
      pathList.value = route.matched.filter(item => item.meta && item.meta.title);
    };
    onMounted(() => {
      getCurrentPath();
    });
    watch(() => route, (to, from) => {
      pathList.value = to.matched.filter(item => item.meta && item.meta.title);
      console.log(to);
      console.log(from);
    }, {
      immediate: true
    });
    return {
      pathList
    };
  },
  data() {
    return {
      myChart: {},
      pieData: [{
        value: 15,
        name: ""
      }, {
        value: 15,
        name: ""
      }, {
        value: 15,
        name: ""
      }, {
        value: 15,
        name: ""
      }, {
        value: 5,
        name: ""
      }, {
        value: 20,
        name: ""
      }],
      pieName: [],
      myChartStyle: {
        float: "left",
        width: "100%",
        height: "300px"
      },
      //
      showInfoBox: false,
      // 
      selectedInfo: "",
      // 
      selectedInfoImage: null,
      //
      markers: [{
        top: 430,
        left: 950,
        info: "",
        image: require("@/assets/jing/01.png")
      }, {
        top: 460,
        left: 1150,
        info: "",
        image: require("@/assets/jing/02.png")
      }, {
        top: 550,
        left: 1250,
        info: "",
        image: require("@/assets/jing/04.png")
      }, {
        top: 200,
        left: 920,
        info: "",
        image: require("@/assets/jing/03.png")
      }],
      infoBoxTop: 0,
      // 
      infoBoxLeft: 0 // 
    };
  },
  mounted() {
    this.initDate(); //
    this.initEcharts();
  },
  created() {
    this.updateLocalTime();
  },
  methods: {
    updateLocalTime() {
      // 
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, '0');
      const minutes = now.getMinutes().toString().padStart(2, '0');
      const seconds = now.getSeconds().toString().padStart(2, '0');
      this.localTime = `${hours}:${minutes}:${seconds}`;

      // 
      setTimeout(this.updateLocalTime, 1000);
    },
    initDate() {
      for (let i = 0; i < this.pieData.length; i++) {
        // this.xData[i] = i;
        // this.yData =this.xData[i]*this.xData[i];
        this.pieName[i] = this.pieData[i].name;
      }
    },
    initEcharts() {
      // 
      const option = {
        legend: {
          // 
          data: this.pieName,
          right: "10%",
          top: "30%",
          orient: "vertical"
        },
        title: {
          // 
          text: "",
          top: "5%",
          left: "center"
        },
        color: ['#ed5126', '#057748', '#2e4e7e', '#ffc773', '#806d9e', '#c2ccd0', '#619ac3'],
        series: [{
          type: "pie",
          label: {
            show: true,
            formatter: "{b} : {c} ({d}%)" // bcd
          },
          radius: "40%",
          //
          data: this.pieData
        }]
      };
      // console.log(this.seriesData);
      const optionFree = {
        xAxis: {},
        yAxis: {},
        series: [{
          data: this.seriesData,
          type: "line",
          smooth: true
        }]
      };
      this.myChart = echarts.init(document.getElementById("mychart"));
      this.myChart.setOption(option);
      //
      window.addEventListener("resize", () => {
        this.myChart.resize();
      });
    },
    checkMarker(event) {
      // 
      const x = event.offsetX;
      const y = event.offsetY;
      for (const marker of this.markers) {
        if (Math.abs(marker.top - y) < 10 && Math.abs(marker.left - x) < 10) {
          // 
          this.showInfo(marker);
          break;
        } else {
          this.selectedInfo = null;
          this.showInfoBox = false; // 
          this.selectedInfoImage = null;
        }
      }
    },
    showInfo(marker) {
      this.showInfoBox = true; // 
      this.selectedInfo = marker.info;
      this.selectedInfoImage = marker.image;
      this.infoBoxTop = marker.top; // 
      this.infoBoxLeft = marker.left + 20; // 
    }
  }
};